"use client"

import React from "react"
import { usePathname } from "next/navigation"
import { AppShell } from "@/components/app-shell"

type BreadcrumbItem = { label: string; href?: string }

function pretty(seg: string) {
  const map: Record<string, string> = {
    dashboard: "Dashboard",
    controls: "Controles",
    execution: "Execução",
    reviews: "Revisões",
    "action-plans": "Planos de Ação",
    admin: "Admin",
  }

  return map[seg] ?? seg.charAt(0).toUpperCase() + seg.slice(1)
}

function buildBreadcrumbs(pathname: string): BreadcrumbItem[] {
  const segments = pathname.split("/").filter(Boolean)

  if (segments.length === 0) return []

  const crumbs: BreadcrumbItem[] = []
  let acc = ""

  for (let i = 0; i < segments.length; i++) {
    const seg = segments[i]
    acc += `/${seg}`

    if (i === segments.length - 1) {
      crumbs.push({ label: pretty(seg) })
    } else {
      crumbs.push({ label: pretty(seg), href: acc })
    }
  }

  return crumbs
}

export function AppShellClient({ children }: { children: React.ReactNode }) {
  const pathname = usePathname()

  const breadcrumbs = React.useMemo(() => {
    return buildBreadcrumbs(pathname || "/")
  }, [pathname])

  return <AppShell breadcrumbs={breadcrumbs}>{children}</AppShell>
}
